}
/*leave*/
.itemTodo-leave-active[data-v-6dfb9b2b]{
  -webkit-transform-origin: 50% 0%;
          transform-origin: 50% 0%;
  -webkit-animation: slide-out-data-v-6dfb9b2b 0.5s ease-out forwards;
          animation: slide-out-data-v-6dfb9b2b 0.5s ease-out forwards;
}
/*keyframe*/
@-webkit-keyframes slide-in-data-v-6dfb9b2b {
0% {
    -webkit-transform:scale(1) rotateX(90deg);
